.app {
	position: relative;
	height: 100%;
	width: fit-content;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.app img {
	max-width: 100%;
	width: max-content;
}

.app > div {
	display: flex;
	justify-content: center;
	align-items: center;
	justify-content: center;
}

.app > div > p {
	margin-top: 0.1rem;
	position: absolute;
	font-size: clamp(5px, 4vw, 1.1rem);
	text-align: center;
}

.app > p {
	max-width: 900px;
	font-size: clamp(5px, 4vw, 1.1rem);
	direction: rtl;
	text-align: justify;
	line-height: clamp(5px, 2rem, 5rem);
}
